import React from 'react';

const Book = (props) => {
    const {img, title, author} = props
    // attribute, eventHandler
    // onClick, onMouseOver
    const clickHandler = () => {
        alert('hellow')
    }

    const complexExample = (author) => {
        console.log(author)
    }
    return (
        <article className='book' onMouseOver={() => {
            console.log(title)
        }}>
            <img
                src={img}
                alt="Book"/>
            <h1>{title}</h1>
            <h4>{author}</h4>
            <button type='button' onClick={clickHandler}>Butoon</button>
            <button type='button' onClick={() => complexExample(author)}>Complex</button>
        </article>
    )
}

export default Book;